<template>
	<div id="search">
    <div>
     <mt-search v-model="search" cancel-text="取消" placeholder="搜一搜" :result.sync="result">
      <mt-cell v-for="(item,index) in result" @click.native="gotoDetail(index)" :title="item.title" >
      </mt-cell>
    </mt-search>
  </div>
</div>
</template>

<script>
import config from "../config.js"
import util from "../utils/commonUtils"
export default {
  name: 'Content',
  data () {
    return {
      search:null,
      result:[],
      noResult:[{
        title:"没有搜索到相应问题，请您更换关键字！",
        id:0
      }],
      msg: 'Welcome to Your Vue.js App'
    }
  },
  watch:{
    search:function(newVal,oldVal){
      console.log("newVal:"+newVal);
      console.log("oldVal:"+oldVal);
      let val=util.removeAllSpace(newVal);
      if(val==""||val==null||val==" "){
        return;
      }
      this.searchInfos(val);
    }
  },
  created:function(){  
    let titleObject={
      back:true,
      title:"客户服务搜索"
    }
    this.$emit("routerChange",titleObject);
  },
  methods:{ 
    searchInfos:function(key){
      let url = config.search;
      let param={
        key:key
      }
      let that = this;
      this.$http.post(url,param).then(function(response){
        let data=response.body;
        if(data.resCode==0){
          if(data.result.length>0){
            that.result=data.result;
          }else{
            that.result=that.noResult;
          }
        }
      });
    },
    gotoDetail:function(index){
      console.log(index);
      let id =this.result[index].id;
      if(id>0){
       this.$router.push({name:"content",params:{id:id}});
      }
    }
  }
}
</script>

  <style>
  #search{
    font-size: 1.4rem;
  }
  .mint-search-list{
    padding-top: 90px;
  }
  .mint-searchbar-core{
    margin-left: 3px;
  }
  #search .mint-cell-wrapper{
    font-size: 1.4rem;
  }

  </style>


