<template>
	<div id="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-xs-1">
          <div class="question-prefix">
            <span>Q:</span>
          </div>
        </div>
        <div class="col-xs-11">
          <div class="row-extra">
            <h3>{{infoDetail.title}}</h3>
          </div>
        </div>
      </div>
      <hr />
      <div class="row">
        <div class="col-xs-1">
          <div class="question-prefix extra">
            <span class="question-prefix">A:</span>
          </div>
        </div>
        <div class="col-xs-11">
          <div class="row-extra">
            <div v-html="infoDetail.content">
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from "../config.js"
export default {
  name: 'Content',
  data () {
    return {
      infoDetail:{
        title:""
      },
      msg: 'Welcome to Your Vue.js App'
    }
  },
  created:function(){  
    let titleObject={
      back:true,
      title:"查看解决方案"
    };
    this.$emit("routerChange",titleObject);
    console.log(this.$route.params.id);
    let id =this.$route.params.id;
    this.getInfo(id);
  },
  methods:{
    getInfo:function(index){
      let utilUrl = config.getInfo;
      let url = utilUrl+"/"+index;
      let that = this;
      this.$http.get(url).then(function(response){
        let data=response.body;
        if(data.resCode==0){
          that.infoDetail=data.result;
        }
      });
    }
  }
}
</script>

<style>
#content {
  font-size: 1.4rem;
  overflow: hidden;
}
#content .question-prefix{
  font-style: italic;
  font-size: 2rem;
  color: #D5D5D5;
  margin-top: 8px;
  font-weight:800;
}
#content .question-prefix.extra{
  margin-top: 1px;
}
#content .row-extra{
  margin-left: -6px;
  margin-top: 5px;
}

   


</style>


