<template>
	<div id="app">
		<div class="container-fluid main-nav" v-show="titleObject.back">
			<div class="row">
				<mt-header  :title="titleObject.title">
					<div @click="goBack" slot="left">
						<mt-button icon="back"></mt-button>
					</div>
				</mt-header>
			</div>
		</div>
		<div>
			<router-view v-on:routerChange="routerChange"></router-view>
		</div>
	</div>
</template>

<script>
export default {
	name: 'app',
	data () {
		return {
			titleObject:{
				back:false,
				title:""
			}
		}
	},
	created:function(){

	},
	methods:{
		goBack:function(){
			this.$router.push({ name: 'main' });
		},
		routerChange:function(data){
			this.titleObject=data;
		}
	}
}
</script>

<style>
#app {
	font-family: 'Songti SC','Heiti TC','Kaiti SC','RS_FangSong', Helvetica, 'Avenir', Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	width: 100%;
}
#app .main-nav h1{
	margin: 0;
	padding-top: 5px;
	padding-bottom: 5px;
	font-size: 1.6rem;
}
#app .mint-header{
	background-color: #F14856;
}
.mint-indicator-mask{
	opacity: 0.1;
	background-color: #000;
}
#app .mint-cell{
	min-height: 45px;
}
</style>



