<template>
	<div id="main" class="container-fluid">
    <div class="row title">
      <div class="col-xs-6 title-lead-wrap">
        <span class="title-lead">帮助中心</span>
      </div>
      <div class="col-xs-6">
        <div class="searchbar-inner" @click="gotoSearch">
          <i class="mintui mintui-search"></i> 
          <span class="mintui-search-text">搜一搜</span>
        </div>
      </div>
    </div>
    <div class="row help-nav">
      <div class="col-xs-12 allSortWrap">
        <div class="allSort">
          <div class="sortMenu clearfix">
            <mt-navbar class="sortMenu-ul" v-model="selected">
              <mt-tab-item class="cell" @click.native="getInfos(index)" v-bind:id="index+1" v-bind:key="index" v-for="(item,index) in categoryList">
                {{item.title}}
              </mt-tab-item>
            </mt-navbar>
          </div>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-xs-12">
        <mt-tab-container v-model="selected">
          <mt-tab-container-item  id="1" style="display:block;">
            <mt-cell v-bind:key="index" v-for="(info,index) in infoList" :title="info.title" @click.native="gotoDetail(index)" />
          </mt-tab-container-item>
        </mt-tab-container>
      </div>
    </div>
  </div>
</template>

<script>
import config from "../config.js"
import { Indicator } from 'mint-ui';
export default {
  name: 'Main',
  data () {
    return {
      search:null,
      selected:1,
      categoryList:null,
      infoList:null,
      msg: 'Welcome to Your Vue.js App'
    }
  },
  created:function(){  
    let titleObject={
      back:false,
      title:"客户服务"
    };
    this.$emit("routerChange",titleObject);
    this.initCategorys();
    
  },
  methods:{
    initCategorys:function(){
      let url = config.getCategorys;
      let that = this;
      Indicator.open();
      this.$http.get(url).then(function(response){
        Indicator.close();
        let data=response.body;
        if(data.resCode==0){
          that.categoryList=data.result;
          that.getInfos(0);
        }
      });
    },
    gotoSearch:function(){
      this.$router.push({name:"search"});
    },
    getInfos:function(index){
      Indicator.open();
      let utilUrl = config.getInfosByCategoryId;
      let category = this.categoryList[index];
      let url = utilUrl+"/"+category.id;
      let that = this;
      this.$http.get(url).then(function(response){
        Indicator.close();
        let data=response.body;
        if(data.resCode==0){
          that.infoList=data.result;
        }
      });
    },
    gotoDetail:function(index){
      console.log(index);
      let id =this.infoList[index].id;
      console.log(id);
      this.$router.push({name:"content",params:{id:id}});
    },
    gotoDetail:function(index){
      console.log(index);
      let id =this.infoList[index].id;
      console.log(id);
      this.$router.push({name:"content",params:{id:id}});
    },
    gotoDetail:function(index){
      console.log(index);
      let id =this.infoList[index].id;
      console.log(id);
      this.$router.push({name:"content",params:{id:id}});
    },
    imageLoad:function($event){
      if($event.target){
          //$event.target.parentNode.parentNode.firstChild.style.height=$event.target.height+"px";
        }
      }

    }
  }
  </script>

  <style>
  #main .title{
    margin-top: 10px;
    margin-bottom: 10px;
  }
  #main .help-nav{
    margin-top: 20px;
    margin-bottom: 20px;
  }

  #main .title .title-lead-wrap{
    padding-top: 5px;
    padding-bottom: 5px;
  }
  #main .title .title-lead{
    color: #000000;
    font-size: 1.6rem;
  }
  #main .allSortWrap.col-xs-12{
    padding-left: 0;
    padding-right: 0;
  }
  .searchbar-inner{
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    background-color: #F5F5F5;
    border-radius: 15px;
    padding: 5px 10px;
  }
  .searchbar-inner .mintui-search{
    font-size: 1.4rem;
    color: #d9d9d9;
  }
  .searchbar-inner .mintui-search-text{
    width: 80%;
    font-size: 1.2rem;
    color: #515151;
    padding-top: 2px;
    text-align: center;
  }
  .allSort{

  }

  .sortMenu{
    width: 100%; 
    background-color:#fff; 
    overflow-x: scroll; 
    -webkit-overflow-x: scroll;
  }
  .sortMenu::-webkit-scrollbar{ 
    width: 0; 
    height: 0;   
    background-color: #fff;  
  }
  .sortMenu-ul { 
    min-width:500px; 
    display: flex;
    justify-content: flex-start;
  }
  .sortMenu .cell{ 
    display: inline-block; 
    font-size: 1.4rem;
    margin: 0px 0.6rem;
    height: 22px;
    line-height:22px;
    text-align: center;
    position: relative;
    text-overflow: ellipsis;
    word-break: keep-all;
  }
  .sortMenu .cell.special a{
    color: #ff474c;
  }
  .sortMenu .cell.special:before {
    content: '';
    height: 2px;
    width: 100%;
    background: #ff474c;
    position: absolute;
    bottom: 0px;
  }
  .sortMenu .all{
    right: 0;
    top: 0;
    height: 30px;
    width: 30px;
    position: absolute;
    background: #fff;
    z-index: 10;
    display: flex;
    justify-content:center;
    align-items:center;
  }
  .sortMenu .all:before{
    content: '';
    height: 25px;
    width: 1px;
    position: absolute;
    box-shadow: 1px 0px 1px #e0e0e0;
    left: 0px;
  }
  #main a:hover, a:focus{
    text-decoration: none;
  }
  #main .allSort, a{
    color: #787878;
  }
  #main  a.is-selected{
    color: red;
    border: 0.5px solid;
    border-radius: 15px;
    padding: 0 5px;
  }
  #main .mint-tab-item{
    padding: 1px 6px;
  }
  #main .mint-tab-item .mint-tab-item-label{
   font-size: 1.4rem;
   line-height: 1.4;
  }
  #main .mint-navbar > a:first-child{
    margin-left: 10px;
  }
  #main .mint-navbar > a:last-child{
    margin-right: 10px;
  }
  #main .mint-cell-wrapper{
    padding: 0;
    font-size: 1.4rem;
   /* border-bottom: 0.5px solid #F1F1F1;*/
  }

   #main .mint-tab-item-label{
    padding-top: 2px;
   }



</style>


